  box-shadow: inset 0 0 0 2px var(--bs-gray-500);
  transition: all 0.3s ease-in-out;
  border: 0;
}
.tns-nav [data-nav]:after {
  border-radius: 50%;
  overflow: hidden;
  content: "";
  position: absolute;
  bottom: 0;
  height: 0;
  left: 0;
  width: 100%;
  background-color: var(--bs-gray-500);
  box-shadow: 0 0 1px var(--bs-gray-500);
  -webkit-transition: height 0.3s ease-in-out;
  transition: height 0.3s ease-in-out;
}
.tns-nav .tns-nav-active:after {
  height: 100%;
}

.dots-inside .tns-nav {
  top: auto;
  padding-bottom: 20px;
}

.dots-primary .tns-nav [data-nav] {
  box-shadow: inset 0 0 0 2px var(--bs-primary);
  transition: all 0.3s ease-in-out;
}
.dots-primary .tns-nav [data-nav]:after {
  background-color: var(--bs-primary);
  box-shadow: 0 0 1px var(--bs-primary);
}

.dots-white .tns-nav [data-nav] {
  box-shadow: inset 0 0 0 2px var(--bs-white);
  transition: all 0.3s ease-in-out;
}
.dots-white .tns-nav [data-nav]:after {
  background-color: var(--bs-white);
  box-shadow: 0 0 1px var(--bs-white);
}

.dots-dark .tns-nav [data-nav] {
  box-shadow: inset 0 0 0 2px #191a1f;
  transition: all 0.3s ease-in-out;
}
.dots-dark .tns-nav [data-nav]:after {
  background-color: #191a1f;
  box-shadow: 0 0 1px #191a1f;
}

.dots-creative .tns-nav [data-nav] {
  background: var(--bs-gray-300);
  border: none;
  width: 12px;
  height: 12px;
  border-radius: 10px;
  box-shadow: none;
}
.dots-creative .tns-nav [data-nav]:after {
  content: none;
}
.dots-creative .tns-nav .tns-nav-active {
  background: var(--bs-primary);
  width: 24px;
}

.custom-thumb [data-nav] {
  cursor: pointer;
  opacity: 0.6;
}
.custom-thumb [data-nav] * {
  color: var(--bs-white) !important;
}
.custom-thumb .tns-nav-active[data-nav] {
  opacity: 1;
}

.tns-nav-line .tns-nav [data-nav] {
  background: var(--bs-gray-300);
  border: none;
  width: 30px;
  height: 3px;
  border-radius: 3px;
  box-shadow: none;
}
.tns-nav-line .tns-nav [data-nav]:after {
  content: none;
}
.tns-nav-line .tns-nav .tns-nav-active {
  background: var(--bs-primary);
}

.text-primary-hover a {
  color: var(--bs-gray-400);
}
.text-primary-hover a:hover {
  color: var(--bs-primary) !important;
}

a.text-primary-hover, button.text-primary-hover {
  color: var(--bs-gray-400);
}
a.text-primary-hover:hover, button.text-primary-hover:hover {
  color: var(--bs-primary) !important;
}

.text-primary-hover li {
  color: var(--bs-gray-400);
}
.text-primary-hover li:hover {
  color: var(--bs-primary) !important;
}

.font-base {
  font-family: "Rubik", sans-serif !important;
}

.font-heading {
  font-family: "Nunito Sans", sans-serif !important;
}

.grayscale {
  filter: grayscale(100%);
  transition: all 0.6s ease;
}
.grayscale:hover {
  filter: none;
  -webkit-filter: grayscale(0%);
}

.zoom-hover {
  transform: scale(1);
}
.zoom-hover:hover {
  transform: scale(1.2);
}

.up-hover {
  transition: all 0.3s ease-in-out;
}
.up-hover:hover {
  transform: translateY(-10px);
}

.icon-sm {
  width: 1.5rem;
